	module.exports = {"data":{"markdownRemark":{"html":"<h3>JavaScript And The Web</h3>\n<p><a href=\"https://egghead.io/courses/manage-application-state-with-mobx-state-tree\">Manage Application State with Mobx-state-tree| Michael Weststrate on Egghead</a>  - Mobx-state-tree is an opinionated state library built on top of the unopinionated Mobx library.  This is a free course explaining why it exists and how to use it.  I <a href=\"https://benmccormick.org/2017/01/09/mobx-first-impressions/\">wrote about Mobx last year</a> and am still enjoying using it a year later.  I’m excited to dive into Mobx-state-tree and hopefully sharpen my Mobx skills in the process.</p>\n<h3>Programming Tools And Practices</h3>\n<p><a href=\"http://blog.atom.io/2018/01/10/the-state-of-atoms-performance.html\">The State of Atom’s Performance | Atom Blog</a> - Atom continues to (slowly) become snappier.  I’m glad to see how organized they are on this.  It’s really hard to take something that was built with a slower architecture fast while it’s being used by tens of thousands of people every day.  They’re making great progress.</p>\n<p><a href=\"http://amzn.to/2DqBeSQ\">Clean Architecture | Robert C Martin</a> - I’ll probably be posting more on this soon, but I’ve been reading the new book on architecture by “Uncle Bob” Martin, and it’s fantastic.  It breaks down big architecture and program design concepts like the Single Responsibility Principle and Dependency Inversion, and explains them well individually, but also shows how they fit together when designing a large system.  I’ll write a full review when I finish the book, but for now just wanted to share that it is worth picking up.</p>\n<h3>Non-Tech</h3>\n<p><a href=\"https://www.nytimes.com/2018/01/09/us/north-carolina-gerrymander.html\">North Carolina Is Ordered To Redraw Its Congressional Map | NY Times</a> - A little bit of local news here <sup id=\"fnref-1\"><a href=\"#fn-1\" class=\"footnote-ref\">1</a></sup>, but it is still important for everyone in the USA.  Redistricting is the type of problem where a simple rules based algorithm could be helpful.  Nothing opaque or deep-learning-y, just have a machine apply a published set of rules and work with the results.</p>\n<div class=\"footnotes\">\n<hr>\n<ol>\n<li id=\"fn-1\">\n<p>I’m a proud resident of Durham, North Carolina</p>\n<a href=\"#fnref-1\" class=\"footnote-backref\">↩</a>\n</li>\n</ol>\n</div>","frontmatter":{"title":"Weekly Links: January 12th","keywords":"gerrymandering mobx-state-tree atom.io","category":"opinion","date":"2018/01/12","path":"/2018/01/12/weekly-links-01-12-18/","layout":"weekly-links","hideFooter":null},"fields":{"slug":"/2018/01/12/weekly-links-01-12-18/"}}},"pathContext":{"slug":"/2018/01/12/weekly-links-01-12-18/","relatedPosts":[{"path":"/2013/06/09/90-done-halfway-there","data":{"title":"90% Done, Halfway There","path":"/2013/06/09/90-done-halfway-there","description":"A post on the difficulties of estimating development projects","date":"2013-06-09T18:45:00+00:00"}},{"path":"/2013/01/25/career-fairs-how-to-not-get-hired-and-how-to-give-yourself-a-chance","data":{"title":"Career Fairs: How To Not Get Hired (And How To Give Yourself A Chance)","path":"/2013/01/25/career-fairs-how-to-not-get-hired-and-how-to-give-yourself-a-chance","description":"What to do when you want a job","date":"2013-01-25T19:30:00+00:00"}},{"path":"/2013/06/08/customer-culture-revisited","data":{"title":"Customer Culture Revisited","path":"/2013/06/08/customer-culture-revisited","description":"Talking about how Microsoft culture impacts their products","date":"2013-06-08T18:45:00+00:00"}}]}}

